#Container-Redirect {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

#Container-Redirect > h1 {
  font-family: Play;
  font-style: normal;
  font-weight: bold;
  margin-top: 16px;
  margin-bottom: 0;
  color: #393939;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
}

#LinkHome {
  font-family: Play;
  font-style: normal;
  font-weight: bold;
  color: #414141;
  font-size: 18px;
  margin-bottom: 15px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

#Container-Links {
  min-width: 40%;
  max-height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #FDFDFD;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 16px;
}

#Container-Links > h2 {
  font-family: Play;
  font-style: normal;
  font-weight: bold;
  line-height: 25px;
  text-align: center;
  color: #003049;
}

#ListLinks {
  width: 100%;
  height: 100%;

  overflow: hidden scroll;

  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around; */
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(202, 202, 202);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(185, 185, 185);
  border-radius: 10px;
}

@media (max-width: 41.75em) {
  #Container-Links {
    min-width: 20%;
  }
}
